//store연결

import Vue from 'vue'
import Vuex from 'vuex'
import router from './router'
import axios from 'axios'
import VueQrcodeReader from "vue-qrcode-reader";
import * as authApi from '@/api/auth'
import 'url-search-params-polyfill';
import * as index from '@/api/index'

Vue.use(Vuex);
Vue.use(VueQrcodeReader);





export default new Vuex.Store({
    state: {
        userInfo: null, //로그인된 정보는 info에 저장해준다.
        allUsers: [], //DB조회 부분 임시 중앙 DB역할
        isLogin: false, //로그인 성공
        isLoginError: false, //로그인 에러
        agencyName: null, // 대리점 명으로 개인화

    },
    mutations: { //실행을 하기위해 commit 명령어 사용

        //로그인이 성공시,
        loginSuccess(state, payload) {
            state.isLogin = true //로그인 성공
            state.isLoginError = false // 로그인 에러
            state.userInfo = payload //userInfo로 객체가 저장됨
        },

        //로그인이 실패시,
        loginError(state) {
            state.isLogin = false
            state.isLoginError = true
        },
    },
    actions: {

        login({ commit }, loginObj) { //state부분에 DB역할을 하는 값이
            console.log(loginObj) //입력값1
            console.log(Object.values(loginObj)) //입력값2

            var obj = Object.values(loginObj)
            console.log(typeof(obj[0])); //입력값3

            var params = new URLSearchParams();
            params.append('ENGINEER_ID', loginObj.ENGINEER_ID); //agencyID로 저장
            params.append('ENGINEER_PW', loginObj.ENGINEER_PW);

            console.log(loginObj.ENGINEER_ID) //agencyID로 저장

            axios
                .post('http://192.1.1.30:3000/ind/signin/post', params) //params(body)
                .then(res => {
                    console.log(res);
                    console.log("프론트 post 성공")



                    if (res.data.length === 0) { //안에 데이터가 없으면 오류
                        commit('loginError')
                        alert('이메일과 비밀번호를 확인하세요')

                    } else {
                        commit('loginSuccess', obj[0])
                        router.push({ name: "home" }) //홈으로가기
                    }


                })
                .catch((error) => {
                    commit('loginError')
                    alert('에러 이메일과 비밀번호를 확인하세요')
                    console.log(error)

                })

        },
        logout({ commit }) {
            commit('logout')
            router.push({ name: "signin" })
        }
    }
})