import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import store from './store'

Vue.use(Router)

//연결된 하나만 component 불러옴, 웹페이지 속도 향상
const About = () => {
    return import ('./views/About.vue')
}

const EngineerManage = () => {
    return import ('./views/EngineerManage.vue')
}
const Notice = () => {
    return import ('./views/Notice.vue')
}

const HospitalInfo = () => {
    return import ('./views/HospitalInfo.vue')
}
const EquipmentManage = () => {
    return import ('./views/EquipmentManage.vue')
}

const ServiceStatusManage = () => {
    return import ('./views/ServiceStatusManage.vue')
}
const AsManage = () => {
    return import ('./views/AsManage.vue')
}
const ChangePW = () => {
    return import ('./views/ChangePW.vue')
}
const QrScanner = () => {
    return import ('./views/QrScanner.vue')
}
const EX = () => {
    return import ('./views/EX.vue')
}

const AddServiceReport = () => {
    return import ('./views/AddServiceReport.vue')
}


const Signin = () => {
    return import ('./views/Signin.vue')
}

const Canvas = () => {
    return import ('./views/Canvas.vue')
}





const rejectAuthUser = (to, from, next) => {
    if (store.state.isLogin === true) {
        //이미 로그인된 유저는 로그인 페이지 접속 못함
        next('/') //홈으로 보냄 
        next(window.location.reload())

    } else {
        next() //입장을 허용
    }
}


const onlyAuthUser = (to, from, next) => { //로그인을 안한 상태면 페이지에 접속 불가, 인증된 회원만 받는다.
    if (store.state.isLogin === false) {
        //아직 로그인 안된 유저라서 막음
        alert('로그인을 해야 접근 가능합니다.')
        next('/signin') //로그인페이지로
    } else {
        next() //입장을 허용
    }
}


export default new Router({
    mode: 'history', //해시태그
    base: process.env.BASE_URL,
    routes: [{
            path: '/', //주소
            name: 'home', //추후home으로 변경가능
            // beforeEnter: onlyAuthUser,
            component: Home //연결
        },

        {
            path: '/about',
            name: 'about',
            beforeEnter: onlyAuthUser,
            component: About
        },

        {
            path: '/notice',
            name: 'notice',
            // beforeEnter: onlyAuthUser,

            component: Notice
        },

        {
            path: '/engineerManage',
            name: 'engineerManage',
            beforeEnter: onlyAuthUser,

            component: EngineerManage
        },

        {
            path: '/hospitalInfo',
            name: 'hospitalInfo',
            beforeEnter: onlyAuthUser,

            component: HospitalInfo
        },

        {
            path: '/equipmentManage',
            name: 'equipmentManage',
            beforeEnter: onlyAuthUser,

            component: EquipmentManage
        },

        {
            path: '/serviceStatusManage',
            name: 'serviceStatusManage',
            beforeEnter: onlyAuthUser,

            component: ServiceStatusManage
        },

        {
            path: '/asManage',
            name: 'asManage',
            beforeEnter: onlyAuthUser,

            component: AsManage
        },
        {
            path: '/changePW',
            name: 'changePW',
            beforeEnter: onlyAuthUser,

            component: ChangePW
        },

        {
            path: '/qrScanner',
            name: 'qrScanner',
            // beforeEnter: onlyAuthUser,

            component: QrScanner
        },

        {
            path: '/EX',
            name: 'EX',
            // beforeEnter: onlyAuthUser,

            component: EX
        },

        {
            path: '/AddServiceReport',
            name: 'AddServiceReport',
            beforeEnter: onlyAuthUser,

            component: AddServiceReport
        },



        {
            path: '/signin',
            name: 'signin',
            beforeEnter: rejectAuthUser, //router로 들어가기전에 미리 함수를 실행시키는 기능 가드로 막아줄 수 있음
            component: Signin
        },

        {
            path: '/canvas',
            name: 'canvas',
            component: Canvas
        },










    ]
})